:root {
  --one: papayawhip;
  --two: palevioletred;
  --three: goldenrod;
  --four: rgb(165, 187, 228);
}

.pickerContainer {
  display: flex;
  align-items: center;
  justify-content: center;
}

.picker {
  margin: 16px;
}

.App {
  font-family: sans-serif;
  text-align: center;
}

.one {
  fill: var(--one);
}

.two {
  fill: var(--two);
}

.three {
  fill: var(--three);
}

.four {
  fill: var(--four);
}
